// Dropdown with Icons
.dropdown-icon-item-custom {
  display: block;
  border-radius: 3px;
  line-height: 34px;
  text-align: center;
  padding: 15px 0 9px;
  display: block;
  color: $gray-600;

  img {
      height: 24px;
  }

  span {
      display: block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
  }

  &:hover {
      background-color: rgb(218, 218, 219) !important;
  }
}


