// 
// _header.scss
// 

#page-topbar {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1002;
    background-color: $header-bg;
    border-bottom: 1px solid $border-color;
}

#page-topbar1 {
    position: fixed;
    top: 56px;
    right: 0;
    left: 0;
    z-index: 1002;
    border-bottom: 1px solid $border-color;
}

.navbar-header {
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    height: $header-height;
    padding: 0 5px 0 0;

    .dropdown .show {
        &.header-item {
            background-color: $gray-100;
        }
    }
}

.navbar-brand-box {
    padding: 0 1.5rem;
    width: $navbar-brand-box-width;
    background: $sidebar-bg;
    border-right: 1px solid $sidebar-border-color;
    box-shadow: 0px 2px 2px $sidebar-bg;
}

.logo {
    line-height: 69px;
    color: inherit !important;

    .logo-sm {
        display: none;
    }
}

.logo-txt {
    font-weight: 700;
    font-size: 18px;
    vertical-align: middle;
    margin-left: 5px;
}

.logo-light {
    display: none;
}


body:not([data-sidebar-size="sm"]) {
    #vertical-menu-btn {
        margin-left: -52px;
        margin-right: 20px;
        margin-top: 10px;

        @media (max-width: 991.98px) {
            margin-left: 0;
        }
    }
}

/* Search */

.app-search {
    padding: calc(#{$header-height - 40px} / 2) 0;
    position: relative;

    .form-control {
        border: none;
        height: 40px;
        padding-left: 17px;
        padding-right: 50px;
        margin-top: 10px;
        background-color: $topbar-search-bg;
        box-shadow: none;
    }

    .btn {
        position: absolute;
        right: 3px;
        top: 3px;
        height: 34px;
        padding: 0 10px;
    }
}

.layout-mode-light {
    display: none;
}

body[data-layout-mode="dark"] {

    // .layout-mode-dark {
    //     display: none;
    // }
    .layout-mode-light {
        display: inline-block;
    }
}

// Mega menu

.megamenu-list {
    li {
        position: relative;
        padding: 5px 0px;

        a {
            color: $dropdown-color;
        }
    }
}

@media (max-width: 992px) {
    .navbar-brand-box {
        width: auto;
    }

    .logo {

        span.logo-lg {
            display: none;
        }

        span.logo-sm {
            display: inline-block;
        }
    }
}

.page-content {
    padding-top: calc(#{$header-height} + 60px);
    padding-right: 5px;
    padding-left: 5px;
    padding-bottom: $footer-height +100px;
    // padding: calc(#{$header-height} + #{$grid-gutter-width}+40px) calc(#{$grid-gutter-width} / 2 - 5px) $footer-height calc(#{$grid-gutter-width} / 2 -5px);
}

